import React, { useState } from 'react'
import { FiltersBlock } from '@components/FiltersBlock'
import style from './style.module.css'
import closeIcon from '../../images/close.svg'

const itemsGenres = [
  {value:'1',visible:''}, 
  {value:'2',visible:''}, 
  {value:'3',visible:''},
  {value:'4',visible:''},
  {value:'5',visible:''},
  {value:'6',visible:''},
  {value:'7',visible:''},
]
const itemsCountries= [
  {value:'1',visible:''}, 
  {value:'2',visible:''}, 
  {value:'3',visible:''},
  {value:'8',visible:''},
  {value:'16',visible:''}, 
  {value:'5',visible:''},
  {value:'6',visible:''},
]

export const Filters = () => {
  const [activeFilters,setActiveFilters] = useState<boolean>(false)
  return(
    <>
      <div className={`${style.container} ${activeFilters ? style.activeFilter : ''}`}>
        <FiltersBlock name='genres' title='' items={itemsGenres}/>
        <FiltersBlock name='countries' title='' items={itemsCountries}/>
        <img onClick={() => setActiveFilters(false)} className={style.closedBtn} src={closeIcon} alt="" />
      </div>
      <button className={style.activateBtn} onClick={() => setActiveFilters(true)}></button>
    </>
  )
}